import React from 'react';
import './App.css';
import Game from "./game/Game";
import {CookiesProvider} from "react-cookie";
import {GameContext} from './content/GameContext';
import {Container} from '@mui/material';

interface AppProps<T> {
    context: GameContext<T>;
}

function App<T>(appProps: AppProps<T>) {
    return (
        <CookiesProvider>
            <div className="App">
                <Container>
                    <Game context={appProps.context}/>
                </Container>
            </div>
        </CookiesProvider>
    );
}

export default App;
