import React, {Component, ReactElement, ReactNode} from "react";
import {CellFactory} from "./cells/CellFactory";
import './ResultTable.scss';
import {
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow
} from '@mui/material';
import {DisplayProperties} from '../content/displayproperties/DisplayProperties';
import {BaseCell} from './cells/BaseCell';

export interface ResultTableProperties<T> {
    columns: string[];
    widths: number[];
    rows: DisplayProperties[][];
}

export default class ResultTable<T> extends Component<ResultTableProperties<T>> {
    messagesEndRef = React.createRef<HTMLTableRowElement>()

    constructor(props: ResultTableProperties<T>) {
        super(props);
    }

    private getTitleRow(): ReactElement {
        return <TableRow>
            {this.props.columns.map((item) => {
                return <TableCell key={item} align={'center'} sx={{border: 1, backgroundColor: "#a9a9a9"}}>
                    {item}
                </TableCell>;
            })}
        </TableRow>;
    }

    private convertRowsToCells(): ReactElement[] {
        return this.props.rows.map(
            (row) => {
                const key = row.map(property => property.key).join('_');
                return <TableRow key={key} sx={{height: "100px"}}>
                    {
                        row.map(prop => {
                            let cellType = CellFactory.GetCellTypeFromDisplayPropertyType(prop.type);
                            return <BaseCell key={prop.key} content={cellType} display={prop}/>;
                        })
                    }
                </TableRow>;
            }
        );
    }

    private scrollToBottom = () => {
        this.messagesEndRef?.current?.scrollIntoView({ behavior: 'auto' })
    }

    override componentDidUpdate(): void {
        this.scrollToBottom()
    }

    override render(): ReactNode {
        return <Paper elevation={0} sx={{width: '100%'}}>
            <TableContainer sx={{maxHeight: 500, overflow: 'auto'}}>
                <Table stickyHeader sx={{borderSpacing: 1, borderCollapse: 'separate', width: 'calc(100% - 1px)'}}>
                    <colgroup>
                        {this.props.widths.map((width, i) => <col key={"colWidth_"+i} width={`${width}%`}/>)}
                    </colgroup>
                    <TableHead>
                        {this.getTitleRow()}
                    </TableHead>
                    <TableBody>
                        {this.convertRowsToCells()}
                        <tr ref={this.messagesEndRef} />
                    </TableBody>
                </Table>
            </TableContainer>
        </Paper>;
    }
}
