import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import {GameContext} from './content/GameContext';

async function getContext(): Promise<GameContext<any>> {
    const game = (process.env.CONTEXT || "hertl").toLowerCase();
    switch (game) {
        case "wardle":
            return import('./wardle/Wardle').then((game) => {
                return new game.WardleContext();
            });
        case "ronalde":
            return import('./ronadle/Ronalde').then((game) => {
                return new game.RonaldeContext();
            });
        case "hertl":
        default:
            return import('./hertl/Hertl').then((game) => {
                return new game.HertlContext();
            });
    }
}

getContext().then((game) => {
        ReactDOM.render(
            <React.StrictMode>
                <App context={game}/>
            </React.StrictMode>,
            document.getElementById('root')
        );
    }
);


// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
